
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

* {
	box-sizing: border-box;
}

.main{
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Montserrat', sans-serif;
	height: 100vh;
	margin: -20px 0 50px;
    background: #000000;
    height: 110vh;
    width: 100%;
    overflow: hidden;
}

.h11 {
    color: #DDDDDD;
	font-weight: bold;
	margin: 0;
}



.p11 {
    color: #DDDDDD;
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

.span1 {
	font-size: 12px;
    color: #DDDDDD;
}



.btn {
	border-radius: 20px;
	border: 1px solid #FF4B2B;
	background-color: #FF4B2B;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}



.ghost {
	border-radius: 20px;
	border: 1px solid #FF4B2B;
	background-color: #FF4B2B;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	transition: 0.5s;
	border: 1px whitesmoke solid;
}
.ghost:hover{
	color: #DDDDDD;
	background-color: black;
	
	transition: 0.5s;
}

.form11 {
	background-color: #000000;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 70%;
	margin-top: 18%;
	margin-left: 5%;
	width: 85%;
	text-align: center;
	border-radius: 5%;
	border: 1px solid white;
}

input {
	background-color: #b9afaf;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.container {
	background-color: #000000;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 768px;
	max-width: 100%;
	min-height: 480px;
    
	box-shadow: 0 0 15px 1px white;

}


.form-container {
    
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.sign-in-container {
    background-color:#000000 ;
	/* box-shadow: 0 0 15px 1px white; */

	left: 0;
	width: 50%;
	z-index: 2;
}

.container.right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.container.right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	animation: show 0.6s;
}

@keyframes show {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}
	
	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

.overlay-container {
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.container.right-panel-active .overlay-container{
	transform: translateX(-100%);
}

.overlay {
	background: #000000;
	
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
  	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  	transform: translateX(50%);
}

.overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.overlay-left {
	transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
	transform: translateX(0);
}

.overlay-right {
	right: 0;
	transform: translateX(0);
}

.container.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.social-container {
	margin: 20px 0;
}


.main{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.left{
    width :40%;
}
.left img{
    width: 100%;
}
@media (max-width:700px){
    .main{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
		/* height: 200vh; */
    }
    .right{
        width: 90%;
        
        margin-top: -40%;
    }
    .left{
        width :100%;
        margin-left: 20%;
		margin-bottom: 20%;
    }
    .left img{
        width: 100%;
    }
	
	
	input{
		width: 170%;
	}
	
	.left .logo{
		width: 150px;
		margin-left: -20%;
		/* border-radius: 50%; */
	
	}
}
.left .logo{
	width: 150px;
	margin-left: 5%;
	/* border-radius: 50%; */

}
